"use client"

import type React from "react"
import { createContext, useContext, useState, useEffect } from "react"

export interface Order {
  id: string
  orderNumber: string
  date: string
  items: {
    id: string
    name: string
    price: number
    quantity: number
    image: string
  }[]
  total: number
  status: "pending" | "processing" | "shipped" | "delivered" | "cancelled"
  trackingNumber?: string
  estimatedDelivery?: string
  customerInfo: {
    name: string
    email: string
    phone: string
    address: string
    city: string
    pincode: string
  }
}

export interface User {
  id: string
  name: string
  email: string
  phone: string
  addresses: {
    id: string
    name: string
    email: string
    phone: string
    address: string
    city: string
    pincode: string
    isDefault: boolean
  }[]
  wishlist: string[]
}

interface UserContextType {
  user: User | null
  isLoggedIn: boolean
  orders: Order[]
  login: (email: string, password: string) => void
  signup: (name: string, email: string, password: string, phone: string) => void
  logout: () => void
  addOrder: (order: Order) => void
  updateUserInfo: (updates: Partial<User>) => void
  addAddress: (address: Omit<User["addresses"][0], "id">) => void
  addToWishlist: (productId: string) => void
  removeFromWishlist: (productId: string) => void
}

const UserContext = createContext<UserContextType | undefined>(undefined)

export function UserProvider({ children }: { children: React.ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [orders, setOrders] = useState<Order[]>([])
  const [mounted, setMounted] = useState(false)

  // Load user and orders from localStorage on mount
  useEffect(() => {
    const savedUser = localStorage.getItem("ranaartcart_user")
    const savedOrders = localStorage.getItem("ranaartcart_orders")

    if (savedUser) {
      try {
        setUser(JSON.parse(savedUser))
      } catch (e) {
        console.error("Failed to load user:", e)
      }
    }

    if (savedOrders) {
      try {
        setOrders(JSON.parse(savedOrders))
      } catch (e) {
        console.error("Failed to load orders:", e)
      }
    }

    setMounted(true)
  }, [])

  // Save user to localStorage when it changes
  useEffect(() => {
    if (mounted && user) {
      localStorage.setItem("ranaartcart_user", JSON.stringify(user))
    }
  }, [user, mounted])

  // Save orders to localStorage when they change
  useEffect(() => {
    if (mounted) {
      localStorage.setItem("ranaartcart_orders", JSON.stringify(orders))
    }
  }, [orders, mounted])

  const login = (email: string, password: string) => {
    // Mock authentication - in real app, verify against backend
    if (email && password) {
      const newUser: User = {
        id: `user_${Date.now()}`,
        name: email.split("@")[0],
        email,
        phone: "",
        addresses: [],
        wishlist: [],
      }
      setUser(newUser)
    }
  }

  const signup = (name: string, email: string, password: string, phone: string) => {
    const newUser: User = {
      id: `user_${Date.now()}`,
      name,
      email,
      phone,
      addresses: [],
      wishlist: [],
    }
    setUser(newUser)
  }

  const logout = () => {
    setUser(null)
    localStorage.removeItem("ranaartcart_user")
  }

  const addOrder = (order: Order) => {
    setOrders((prev) => [...prev, order])
  }

  const updateUserInfo = (updates: Partial<User>) => {
    setUser((prev) => (prev ? { ...prev, ...updates } : null))
  }

  const addAddress = (address: Omit<User["addresses"][0], "id">) => {
    setUser((prev) =>
      prev
        ? {
            ...prev,
            addresses: [
              ...prev.addresses,
              {
                id: `addr_${Date.now()}`,
                ...address,
              },
            ],
          }
        : null,
    )
  }

  const addToWishlist = (productId: string) => {
    setUser((prev) =>
      prev && !prev.wishlist.includes(productId)
        ? {
            ...prev,
            wishlist: [...prev.wishlist, productId],
          }
        : prev,
    )
  }

  const removeFromWishlist = (productId: string) => {
    setUser((prev) =>
      prev
        ? {
            ...prev,
            wishlist: prev.wishlist.filter((id) => id !== productId),
          }
        : prev,
    )
  }

  return (
    <UserContext.Provider
      value={{
        user,
        isLoggedIn: !!user,
        orders,
        login,
        signup,
        logout,
        addOrder,
        updateUserInfo,
        addAddress,
        addToWishlist,
        removeFromWishlist,
      }}
    >
      {children}
    </UserContext.Provider>
  )
}

export function useUser() {
  const context = useContext(UserContext)
  if (!context) {
    throw new Error("useUser must be used within UserProvider")
  }
  return context
}
